<template>
    <div class="home" >
        <p class="subtitle-1 text--darken-1 grey--text">Home</p>

        <v-container class="my-5">
            <p class="subtitle-1 text--darken-1 grey--text">Available Kiosks:</p>

            <v-row  no-gutters>
                <v-col cols="12" sm="6" md="4" lg="3" v-for="kiosk in kiosks" :key="kiosk.name">
                    <v-card  class="text-center ma-3 pt-5" hover style="border-left: 4px solid forestgreen;">
                        <v-responsive>
                            <v-avatar size="100" class="grey lighten-2">
                                <img src="./../assets/nmjs.png">
                            </v-avatar>
                        </v-responsive>
                        <v-card-text>
                            <div class="text-subtitle-1 black--text">
                                {{kiosk.location}}
                            </div>
                            <div class="grey-text">
                                {{kiosk.status}}
                            </div>
                        </v-card-text>
                        <v-card-actions>

                        </v-card-actions>
                    </v-card>

                </v-col>
            </v-row>

        </v-container>
        <callingDialog></callingDialog>
    </div>
</template>

<script>

    import callingDialog from "@/components/callingDialog";
    import fb from "@/fb";

    export default {
        name: 'Home',
        data: function(){
            return{
                kiosks: []
            }
        },
        components: {
            callingDialog
        },
        methods: {



        },
        created(){

        },
        mounted: function () {

            fb.firestore().collection('kiosks').onSnapshot(res => {
                const changes = res.docChanges();
                changes.forEach(change => {
                    if (change.type === 'added'){
                        console.log(change.doc.data());
                        this.kiosks.push({
                            ...change.doc.data(),
                            id: change.doc.id
                        })
                    }
                })
            })
            fb.firestore().collection('kiosks').onSnapshot(res => {
                const changes = res.docChanges();
                changes.forEach(change => {
                    if (change.type === 'modified'){
                        console.log(change.doc.data());
                        this.kiosks = []
                        this.kiosks.push({
                            ...change.doc.data(),
                            id: change.doc.id
                        })
                    }
                })
            })


        }
    }
</script>

