<template>
    <v-dialog max-width="500px" max-height="300px" v-model="dialog" persistent >
        <v-card class="pa-3" height="230px" rounded >

            <p class="text-center headline font-weight-light"> You Are Receiving A Call from</p>
            <p class="text-center font-weight-medium "> Santa Fe Magistrate Kiosk</p>

            <v-row >
                <v-col cols="6" class="text-center " >
                    <v-btn class="mx-2" fab dark large color="green" @click="acceptCall">
                        <v-icon dark>mdi-phone</v-icon>
                    </v-btn>
                </v-col>
                <v-col cols="6" class="text-center ">
                    <v-btn class="mx-2" fab dark large color="red" @click="endCall">
                        <v-icon dark>mdi-phone-hangup</v-icon>
                    </v-btn>

                </v-col>
            </v-row>
            <div class="pt-3">
                <v-progress-linear
                        indeterminate
                        reverse
                        striped
                        background-opacity=0
                        color="red darken-2"
                ></v-progress-linear>

                <v-progress-linear
                        indeterminate
                        striped
                        background-opacity=0
                        color="green"
                ></v-progress-linear>

                <v-progress-linear
                        indeterminate
                        reverse
                        striped
                        background-opacity=0
                        color="red darken-2"
                ></v-progress-linear>

                <v-progress-linear
                        indeterminate
                        striped
                        background-opacity=0
                        color="green"
                ></v-progress-linear>
            </div>

        </v-card>

    </v-dialog>
</template>

<script>
    import fb from "@/fb";


    export default {
        name: 'callingDialog',
        components: {
        },
        data: function () {
            return{
                dialog: false,
            }
        },
        methods:{
            acceptCall: function () {
                const that = this;

                this.$router.push({ path: '/video'});


                var x = document.getElementById("myAudio");
                x.pause();
                that.dialog = false;

                var kiosk = fb.firestore().collection('kiosks').doc('mg-kiosk');

                kiosk.update({
                    calling:false
                })



            },
            endCall: function () {
                const that = this;
                console.log("ended");
                var x = document.getElementById("myAudio");
                x.pause();
                that.dialog = false;

                var kiosk = fb.firestore().collection('kiosks').doc('mg-kiosk');

                kiosk.update({
                    calling:false
                })
            }
        },
        mounted: function () {
            const that = this;

            fb.firestore().collection('kiosks').onSnapshot(res => {
                const changes = res.docChanges();
                changes.forEach(change => {
                    if (change.type === 'modified'){
                        console.log('Changes: ', change.doc.data());
                        if(change.doc.data().calling === true){
                            var x = document.getElementById("myAudio");
                            x.currentTime = 0;
                            x.play();
                            that.dialog = true;
                        }
                    }
                })
            })

        }
    }

</script>

