<template>
    <nav>
        <v-app-bar
                app
                color="primary"
                dark
                clipped-left
        >
            <v-app-bar-nav-icon @click="drawer = !drawer" class="white--text"></v-app-bar-nav-icon>
            <v-toolbar-title class="text-uppercase white--text">
                <span class="font-weight-bold">NMJS|</span>
                <span class="font-weight-light">dashboard (Court Clerk)</span>
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items>
                <v-btn text>
                    <span>Court Clerk</span>
                    <v-icon right>mdi-account</v-icon>
                </v-btn>
                <v-btn text>
                    <span>Sign-out</span>
                    <v-icon right>mdi-logout-variant</v-icon>
                </v-btn>
            </v-toolbar-items>
        </v-app-bar>

        <v-navigation-drawer v-model="drawer" app clipped color="primary">

            <v-row>
                <v-col class="text-center">
                    <v-card flat class=" ma-2" color="rgba(0, 0, 0, 0.15)">
                        <h2 class="white--text pt-2 pb-3 " >Interpreters</h2>
                        <v-divider></v-divider>
                        <v-list >
                            <v-list-item v-for="(interpreter, i) in interpreters" :key="i" >
                                <v-list-item-content @click="callInterpreter">
                                    <v-btn class="mx-2" flat text color="white" > {{interpreter.interpreterName}}
                                        <v-icon dark right :color="interpreter.status">mdi-checkbox-blank-circle</v-icon>
                                    </v-btn>

                                </v-list-item-content>
                            </v-list-item>

                        </v-list>
                    </v-card>
                </v-col>
            </v-row>

            <v-row>
                <v-col class="text-center">
                    <v-card flat class=" ma-2" color="rgba(0, 0, 0, 0.15)">
                        <h2 class="white--text pt-2 pb-3 " >Judges</h2>
                        <v-divider></v-divider>
                        <v-list >
                            <v-list-item v-for="(judge, i) in judges" :key="i" >
                                <v-list-item-content @click="callJudge">
                                    <v-btn class="mx-2" flat text color="white" > {{judge.judgeName}}
                                        <v-icon dark right :color="judge.status">mdi-checkbox-blank-circle</v-icon>
                                    </v-btn>

                                </v-list-item-content>
                            </v-list-item>

                        </v-list>
                    </v-card>
                </v-col>
            </v-row>

        </v-navigation-drawer>
    </nav>

</template>

<script>
    import fb from "@/fb";

    export default {
        name: 'navBar',
        data() {
            return{
                drawer: true,
                interpreters: [],
                judges: []
            }
        },
        methods:{

            callJudge(){
                fb.firestore().collection('judges').onSnapshot(res => {
                    const changes = res.docChanges();
                    changes.forEach(change => {
                        if (change.type === 'added'){
                            console.log(change.doc.data());
                            var judge = fb.firestore().collection('judges').doc(change.doc.id);

                            judge.update({
                                calling: true
                            })
                                .then(() => {console.log("Successfully updated calling to TRUE")})
                                .catch((er) => {console.log("ERROR:", er)});
                        }
                    })
                })
            },
            callInterpreter(){
                fb.firestore().collection('interpreters').onSnapshot(res => {
                    const changes = res.docChanges();
                    changes.forEach(change => {
                        if (change.type === 'added'){
                            console.log(change.doc.data());
                            var judge = fb.firestore().collection('interpreters').doc(change.doc.id);

                            judge.update({
                                calling: true
                            })
                                .then(() => {console.log("Successfully updated calling to TRUE")})
                                .catch((er) => {console.log("ERROR:", er)});
                        }
                    })
                })

            }


        },

        mounted: function () {


            fb.firestore().collection('judges').onSnapshot(res => {
                const changes = res.docChanges();
                changes.forEach(change => {
                    if (change.type === 'added'){
                        console.log(change.doc.data());
                        this.judges.push({
                            ...change.doc.data(),
                            id: change.doc.id
                        })
                    }
                })
            });
            fb.firestore().collection('judges').onSnapshot(res => {
                const changes = res.docChanges();
                changes.forEach(change => {
                    if (change.type === 'modified'){
                        console.log(change.doc.data());
                        this.judges = [];
                        this.judges.push({
                            ...change.doc.data(),
                            id: change.doc.id
                        })
                    }
                })
            });
            fb.firestore().collection('interpreters').onSnapshot(res => {
                const changes = res.docChanges();
                changes.forEach(change => {
                    if (change.type === 'added'){
                        console.log(change.doc.data());
                        this.interpreters.push({
                            ...change.doc.data(),
                            id: change.doc.id
                        })
                    }
                })
            });
            fb.firestore().collection('interpreters').onSnapshot(res => {
                const changes = res.docChanges();
                changes.forEach(change => {
                    if (change.type === 'modified'){
                        console.log(change.doc.data());
                        this.interpreters = [];
                        this.interpreters.push({
                            ...change.doc.data(),
                            id: change.doc.id
                        })
                    }
                })
            });
        }

    }

</script>

